<template>
  <div class="setting bgfff">
    <h2 class="title">通用配置</h2>
    <div class="setting-content">
      <el-form ref="form" :model="form">
        <el-form-item label="设备认证设置" class="parent-label">
          <span>
            <el-radio v-model="form.deviceSet" label="auto">自动通过</el-radio>
            <el-radio v-model="form.deviceSet" label="manual">手动操作</el-radio>
          </span>
        </el-form-item>
        <el-form-item label="输入设置" class="parent-label">
          <el-row class="sub-title">数据库设置</el-row>
          <el-row>
            <el-form :model="DBset" label-position="left" ref="DBsetForm" label-width="100px">
              <el-form-item label="数据库类型：">
                <el-select
                  v-model="DBset.DBtype.value"
                  @change="selectDBtype"
                  placeholder="请选择数据库类型"
                >
                  <el-option
                    v-for="item in DBset.DBtype.options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="数据库IP端口：" class="DBadress">
                <el-input placeholder="IP address" v-model="DBset.host"></el-input>
                <el-input placeholder="port" v-model="DBset.port"></el-input>
              </el-form-item>
              <el-form-item label="数据库用户名：">
                <el-input placeholder="请输入用户名" autocomplete="new-password" v-model="DBset.username"></el-input>
              </el-form-item>
              <el-form-item label="数据库密码：">
                <el-input
                  placeholder="请输入密码"
                  autocomplete="new-password"
                  type="password"
                  v-model="DBset.password"
                ></el-input>
              </el-form-item>
              <el-form-item label="库名：">
                <el-input placeholder="请输入" v-model="DBset.databaseName"></el-input>
              </el-form-item>
              <el-form-item label="表名：">
                <el-input placeholder="请输入" v-model="DBset.tableName"></el-input>
              </el-form-item>
              <el-form-item label="编码类型：">
                <el-select v-model="DBset.charset.value" placeholder="请选择">
                  <el-option
                    v-for="item in DBset.charset.options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-form>
          </el-row>

          <el-row class="sub-title">文件设置</el-row>
          <el-row type="flex">
            <el-col :span="4">文件路径：</el-col>
            <el-col :span="20">
              <el-input placeholder="请输入" v-model="DBset.filePath"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="输出设置" class="parent-label">
          <span>
            <el-radio v-model="form.outputValue" label="MYSQL" :disabled="!DBset.DBtype.value">数据库</el-radio>
            <el-radio v-model="form.outputValue" label="FILE">写入文件</el-radio>
          </span>
          <div v-if="form.outputValue==='FILE'" class="output-path">
            <span>文件路径：</span>
            <span>
              <el-input v-model="form.outputFilePath"></el-input>
            </span>
          </div>
        </el-form-item>

        <el-form-item label="日志设置" class="parent-label">
          <el-row type="flex">
            <el-col :span="4">日志级别：</el-col>
            <el-col :span="20">
              <el-select v-model="form.log.value" placeholder="请选择">
                <el-option
                  v-for="item in form.log.options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-col>
          </el-row>
          <el-row type="flex">
            <el-col :span="4">输出位置：</el-col>
            <el-col :span="20">
              <el-input placeholder="请输入" v-model="form.outputLogPath"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
    </div>
    <div class="setting-compute-footor">
      <el-button class="normal-button" @click="$router.go(-1)">取消</el-button>
      <el-button @click="postConfig">保存</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      initModel: {},
      form: {
        deviceSet: 'manual',
        outputLogPath: '',
        outputValue: '',
        outputFilePath: '',
        log: {
          value: 'ERROR',
          options: []
        }
      },
      DBset: {
        host: '',
        port: '',
        username: null,
        password: null,
        tableName: null,
        databaseName: null,
        filePath: null,

        DBtype: {
          value: '',
          options: [
            {
              value: -1,
              label: '请选择数据库类型'
            }
          ]
        },
        charset: {
          value: '',
          options: []
        }
      }
    }
  },
  methods: {
    selectDBtype(value) {
      if (value === -1) {
        this.DBset.DBtype.value = ''
      }
    },
    getConfig() {
      this.ApiService.getConfig().then(res => {
        this.initModel = res.data
        const fileInfo = res.data
        this.form.deviceSet = fileInfo.authConfig.authType
        this.form.log.value = fileInfo.logConfig.logLevel
        this.form.outputLogPath = fileInfo.logConfig.logPath
        this.form.outputValue =
          fileInfo.outputConfig.outputFile &&
          fileInfo.outputConfig.outputFile.dataSourceType
        this.form.outputFilePath =
          fileInfo.outputConfig.outputFile &&
          fileInfo.outputConfig.outputFile.tableName
        this.DBset.filePath =
          fileInfo.inputConfig.inputFile &&
          fileInfo.inputConfig.inputFile.tableName
        if (fileInfo.inputConfig.inputDb) {
          this.DBset.DBtype.value = fileInfo.inputConfig.inputDb.dataSourceType
          this.DBset.host = fileInfo.inputConfig.inputDb.host
          this.DBset.port = fileInfo.inputConfig.inputDb.port
          this.DBset.tableName = fileInfo.inputConfig.inputDb.tableName
          this.DBset.username = fileInfo.inputConfig.inputDb.username
          this.DBset.password = fileInfo.inputConfig.inputDb.password
          this.DBset.databaseName = fileInfo.inputConfig.inputDb.databaseName
          this.DBset.charset.value = fileInfo.inputConfig.inputDb.charset
          this.DBset.filePath = fileInfo.inputConfig.inputFile.tableName
        }
      })
    },
    postConfig() {
      let params = {
        authConfig: {
          authType: this.form.deviceSet
        },
        inputConfig: {
          inputDb: {
            charset: this.DBset.charset.value,
            dataSourceType: this.DBset.DBtype.value,
            databaseName: this.DBset.databaseName,
            host: this.DBset.host,
            password: this.DBset.password,
            port: this.DBset.port,
            tableName: this.DBset.tableName,
            username: this.DBset.username
          },
          inputFile: {
            // id: this.initModel.inputConfig.inputFile.id,
            tableName: this.DBset.filePath
          }
        },
        logConfig: {
          logLevel: this.form.log.value,
          logPath: this.form.outputLogPath
        },
        outputConfig: {
          outputDb: {},
          outputFile: {
            // id: this.initModel.outputConfig.outputFile.id,
            tableName: this.form.outputFilePath
          }
        }
      }

      try {
        if (this.initModel.outputConfig.outputFile.id) {
          params.outputConfig.outputFile.id = this.initModel.outputConfig.outputFile.id
        }
        if (this.initModel.inputConfig.inputFile.id) {
          params.inputConfig.inputFile.id = this.initModel.inputConfig.inputFile.id
        }
      } catch (error) {
        console.log(error)
      }

      if (this.form.outputValue === 'MYSQL') {
        params.outputConfig.outputFile = null
      } else {
        params.outputConfig.outputDb = null
      }
      if (!this.form.outputFilePath) {
        params.outputConfig.outputFile = null
      }
      if (!params.inputConfig.inputFile.tableName) {
        params.inputConfig.inputFile = null
      }
      if (
        !this.DBset.charset.value &&
        !this.DBset.DBtype.value &&
        !this.DBset.databaseName &&
        !this.DBset.host &&
        !this.DBset.password &&
        !this.DBset.port &&
        !this.DBset.tableName &&
        !this.DBset.username
      ) {
        params.inputConfig.inputDb = null
      }
      if (
        !this.DBset.charset.value &&
        !this.DBset.DBtype.value &&
        !this.DBset.databaseName &&
        !this.DBset.host &&
        !this.DBset.password &&
        !this.DBset.port &&
        !this.DBset.tableName &&
        !this.DBset.username &&
        !this.DBset.filePath
      ) {
        params.inputConfig = null
      }
      this.ApiService.postConfig(params).then(res => {
        if (res.code === 0) {
          this.Utils.message('操作成功！', 'success')
        } else {
          this.Utils.message(res.message || '保存失败！', 'warning')
        }
      })
    },
    getOptions() {
      this.ApiService.getOptions().then(res => {
        res.data.logLevelEnums.forEach(element => {
          this.form.log.options.push({
            value: element,
            label: element
          })
        })
        res.data.dataSourceTypeEnums.forEach(element => {
          this.DBset.DBtype.options.push({
            value: element,
            label: element
          })
        })

        res.data.charsetEnums.forEach(element => {
          this.DBset.charset.options.push({
            value: element,
            label: element
          })
        })
      })
    }
  },
  mounted() {
    this.getConfig()
    this.getOptions()
  }
}
</script>


<style lang="less" scoped>
.setting {
  height: 100%;
  position: relative;
  .title {
    height: 50px;
    font-size: 14px;
    line-height: 50px;
    font-weight: bold;
    padding-left: 20px;
    border-bottom: 1px solid #ebeef9;
  }
  .setting-content {
    width: 60%;
    margin: 31px auto;
    display: flex;
    flex-direction: column;
  }
  .setting-compute-footor {
    height: 50px;
    line-height: 50px;
    padding: 0px 20px;
    border-top: 1px solid #ebeef9;
    position: absolute;
    bottom: 0px;
    width: 100%;
    text-align: right;
    .el-button {
      width: 96px;
    }
  }
  .output-path {
    display: flex;
    font-size: 12px;
    & > span:nth-of-type(1) {
      width: 100px;
    }
    & > span:nth-of-type(2) {
      flex: 1;
    }
  }
}
</style>
<style lang="less">
.setting {
  .el-form-item__label,
  .el-col,
  .el-radio__label {
    font-size: 12px;
  }
  .el-col-4 {
    width: 100px;
  }
  .el-col-20 {
    flex: 1;
  }
  .el-row {
    margin-bottom: 12px;
  }
  .el-form-item {
    .el-input__inner {
      height: 28px;
      border-radius: 2px;
    }
    .el-select {
      width: 100%;
    }
    .DBadress {
      .el-input {
        &:nth-of-type(1) {
          width: 69%;
        }
        &:nth-of-type(2) {
          width: 30%;
          float: right;
        }
      }
    }
  }
  .parent-label {
    & > .el-form-item__label {
      display: block;
      float: unset;
      width: 100%;
      text-align: left;
      font-size: 14px;
      color: #333333;
      letter-spacing: 0;
      font-weight: bold;
    }
    & > .el-form-item__content {
      padding-left: 20px;
    }
  }

  .sub-title {
    color: #333333;
    letter-spacing: 0;
    line-height: 12px;
    font-size: 12px;
    font-weight: bold;
    margin-top: 25px;
  }
}
.el-select-dropdown__item {
  font-size: 12px;
}
</style>